body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --sub-heading: Outfit;
  --body-text: Roboto;

  /* font sizes */
  --font-size-11xl: 30px;
  --font-size-21xl: 40px;
  --font-size-101xl: 120px;
  --font-size-lg: 18px;
  --sub-heading-size: 20px;
  --body-text-size: 16px;
  --font-size-38xl: 57px;
  --font-size-51xl: 70px;
  --font-size-6xl: 25px;
  --font-size-41xl: 60px;

  /* Colors */
  --color-darkturquoise: #0abbc6;
  --color-black: #000;
  --color-white: #fff;
  --color-lightblue-100: #c7e7f8;
  --color-lightblue-200: #c4e7f7;
  --color-lightblue-300: rgba(198, 231, 248, 0.39);
  --color-lightblue-400: rgba(198, 231, 248, 0.61);
  --color-lightblue-500: rgba(198, 231, 248, 0.54);
  --color-gray: #390808;
  --color-gainsboro: rgba(217, 217, 217, 0);

  /* Paddings */
  --padding-mid: 17px;
  --padding-17xl: 36px;

  /* Border radiuses */
  --br-11xl: 30px;
  --br-51xl: 70px;
  --br-21xl: 40px;
  --br-64xl: 83px;
  --br-6xl: 25px;
  --br-xl: 20px;
}
